* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-form {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

button {
  cursor: pointer;
  background-color: pink;
  border-radius: 50%;
  padding: 15px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

button:focus, button:hover {
  border-color: #f96b83;
}

.gallery {
  flex-wrap: wrap;
  margin-top: 50px;
  display: flex;
}

.photo-card {
  width: 400px;
  margin-bottom: 30px;
  margin-right: 20px;
  display: inline;
}

.photo-card__link {
  width: 100%;
}

.photo-card__img {
  width: 100%;
  display: block;
}

.info {
  border-bottom: 1px solid #c4bfbf;
  border-left: 1px solid #c4bfbf;
  border-right: 1px solid #c4bfbf;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  padding: 20px 15px;
  display: flex;
  box-shadow: 0 4px 4px rgba(129, 135, 135, .2);
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
  font-size: 12px;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

